.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img {
        padding: 0rem 0rem 0rem 0rem;
        width: 20%;
        border-radius: 50%;
    }
    h2 {
        padding: 1rem 1rem 1rem 1rem;
    }
    h3 {
        font-size: 1rem;
        padding-bottom: 1rem;
    }
}
@media screen and (max-width:800px) {
    .song-container {
        img {
            width: 40%;
        }
    }
}